export function ValidRegist(name:string,email:string,password:string,repeatpassword:string):string{
    let nameRegular=/^[a-zA-Z0-9--]+$/;

    if (name.length<5 || name.length>15) return '    5  15 ';
    if (!nameRegular.test(name)) return '   ';

    let emailRegular = /^[a-z.0-9]+@[a-z]{2,}\.[a-z]{2,4}$/;
    if (email.length===0) return ' ';
    if (email.length>40) return ' 40   ';
    if (!emailRegular.test(email)) return '    ';

    if (password!==repeatpassword) return '  ';
    if (password.length<8) return '  ';

    let validpassword = ValidPass(password);
    if (validpassword!=='ok') return validpassword;

    return 'ok'
}

function ValidPass(pass:string):string{
    if (!/[0-9]/.test(pass)) return '   ';
    
    let flag1 = false;
    let flag2 = false;
    for (let char of pass){
        if (/[0-9]/.test(char)) continue;
        if (char.toUpperCase()===char){ 
            flag1=true;
        }
        if (char.toLowerCase()===char){ 
            flag2=true;
        }
        if(flag1 && flag2) break;
    }
    if (!flag1) return '    ';
    if (!flag2) return '    ';

    return 'ok'
}